<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" icon="el-icon-plus" @click="add()">新增</el-button>
      </div>
      <el-table :data="tableData" border class="table" id="multipleTable">
        <el-table-column label="序号">
          <template slot-scope="scope">
            <span>{{scope.$index+(cur_page-1)*cur_size+1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="标题" prop="title">
        </el-table-column>
<!--        <el-table-column label="内容" prop="info">-->
<!--        </el-table-column>-->
        <el-table-column label="图片" prop="userIntegral">
          <template slot-scope="scope">
            <img :src="img+scope.row.minspic" alt="">
          </template>
        </el-table-column>
        <el-table-column label="价格（元）" prop="price">
        </el-table-column>
<!--        <el-table-column label="描述" prop="description">-->
<!--        </el-table-column>-->
        <el-table-column label="修改时间" prop="update_time">
        </el-table-column>
        <el-table-column label="操作" width="280" align="center">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-document" @click="handleDetails(scope.$index, scope.row)">详情
            </el-button>
<!--            <el-button type="text" icon="el-icon-document" @click="goVs(scope.$index, scope.row)">车系-->
<!--            </el-button>-->
            <el-button type="text" icon="el-icon-edit" @click="handleI(scope.$index, scope.row)">编辑</el-button>
            <el-button type="text" icon="el-icon-setting" class="red" @click="open(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog title="新增" :visible.sync="batchVisible" width="600px" center>
      <el-form ref="form" :model="formAdd" :rules="rules">
        <el-form-item label="标题" prop="title">
          <el-input v-model="formAdd.title" autocomplete="off"  class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input v-model="formAdd.price"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
<!--        <el-form-item label="描述" prop="description">-->
<!--          <el-input type="textarea" v-model="formAdd.description"  autocomplete="off" class="mz_wb"></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="详情" prop="info">-->
<!--          <el-input type="textarea" v-model="formAdd.info"  autocomplete="off" class="mz_wb"></el-input>-->
<!--        </el-form-item>-->
        <el-form-item label="图片" prop="minspic">
          <el-upload
            class="avatar-uploader"
            :action="uploadUrl"
            name="image"
            :show-file-list="false"
            :on-preview="handlePictureCardPreview"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button @click="batchVisible = false">取 消</el-button>
                <el-button type="primary" @click="subtijian('form')">确 定</el-button>
            </span>
    </el-dialog>
  </div>
</template>
<script>
  import http from '../../../assets/js/http';

  export default {
    name: 'geren-L2',
    data() {
      return {
        tableData: [],
        total: 0,
        cur_page: 1,
        cur_size: 10,
        img: window.HOST+'/',
        batchVisible:false,
        formAdd:{},
        rules: {
          title: [{
            required: true,
            message: '请填写名称',
            trigger: 'blur'
          }],
          price: [{
            required: true,
            message: '请填写价格',
            trigger: 'blur'
          }],
          // info:[{
          //   required: true,
          //   message: '请填写内容',
          //   trigger: 'blur'
          // }],
          // description: [{
          //   required: true,
          //   message: '请填写描述',
          //   trigger: 'blur'
          // }],
        },
        uploadUrl:window.file,
        imageUrl:''
      }
    },
    created() {
      this.getCategoryData();
    },
    activated(){
      this.getCategoryData()
    },
    computed: {},
    mounted() {

    },
    methods: {
      // 分页导航
      handleCurrentChange(val) {
        this.cur_page = val;
        this.getCategoryData();
      },
      handleSizeChange(val) {
        this.cur_size = val;
        this.getCategoryData();
      },
      //
      getCategoryData() { //
        let that = this;
        const data = {
          "list_rows": parseInt(that.cur_size),
          "page": parseInt(that.cur_page),
          fcolumn:that.$route.query.id
        };
        that.apiPost('/admin/djfwbylm', data).then((res) => {
          that.handelResponse(res, (data) => {
            that.total = data.data.total;
            that.tableData = data.data.data;
          })
        })
      },
      add() {
       this.batchVisible = true;
      },
      subtijian(formName){
        var that = this
        this.$refs[formName].validate((valid) => {
            if(valid) {
              var params = {
                title:that.formAdd.title,
                price:that.formAdd.price,
                cid:that.$route.query.id,
                minspic:that.imageUrl
              };
              that.apiPost('/admin/djfwadd', params).then((res) => {
                this.handelResponse(res, (data) => {
                  that.$message.success('成功');
                  this.batchVisible = false;
                  this.getCategoryData()
                })
              })
            }
        })
      },
      //上传图片
      handlePictureCardPreview(file) {
        this.form.imgUrl = file.url;
        this.imageUrl = file.url;
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.formAdd.imgUrl = res.data;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type == 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;

        /*if(!isJPG) {
        	this.$message.error('上传正确格式图片!');
        }*/
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      //详情
      handleDetails(index, row) {
        this.$router.push({
          path: '/testD',
          query:{
            datoken:row.datoken
          }
        });
      },
      //编辑
      handleI(index, row) {
        this.$router.push({
          path: '/testC',
          query:{
            datoken:row.datoken
          }
        });
      },
      //删除
      open(index, row) {
        var that = this;
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let param = {
            datoken: row.datoken,
          };
          this.apiPost('/admin/djfwdelete', param).then((res) => {
            this.handelResponse(res, (data) => {
              this.$message.success('成功');
              this.getCategoryData()
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    mixins: [http]
  }
</script>

<style scoped>
  img {
    width: 100px;
    height: 100px;
  }
  .handle-box {
    margin-bottom: 20px;
  }
  .del-dialog-cnt {
    font-size: 16px;
    text-align: center
  }
  .table {
    width: 100%;
    font-size: 14px;
  }
  .red {
    color: #ff0000;
  }
  .handle-input {
    width: 300px;
  }
  .el-radio {
    margin-bottom: 20px;
  }
  .el-upload--text img{
    width: 100%;
    height: 100%;
  }
</style>



